export default "import { NgModule } from \"@angular/core\";\nimport { RouterModule } from \"@angular/router\";\nimport { DEMO_PATH_TOKEN, NuiButtonModule, NuiDocsModule, NuiFormFieldModule, NuiTextboxModule, SrlcStage } from \"@nova-ui/bits\";\n\nimport { RuntimeI18NExampleComponent } from \"./runtime-i18n-example.component\";\n\n\nconst routes = [{\n    path: \"\",\n    component: RuntimeI18NExampleComponent,\n    data: {\n        \"srlc\": {\n            \"stage\": SrlcStage.beta,\n        },\n    },\n}];\n\n@NgModule({\n    imports: [\n        NuiButtonModule,\n        NuiDocsModule,\n        NuiFormFieldModule,\n        NuiTextboxModule,\n        RouterModule.forChild(routes),\n    ],\n    declarations: [\n        RuntimeI18NExampleComponent,\n    ],\n    providers: [\n        {\n            provide: DEMO_PATH_TOKEN,\n            useFactory: () => (<any>require).context(`!!raw-loader!./`, true, /.*\\.(ts|html|less)$/),\n        },\n    ],\n    exports: [\n        RouterModule,\n    ],\n})\nexport class RuntimeI18NModule {\n}\n";